"use client"

import type React from "react"
import { useState } from "react"
import { useAuth } from "@/context/auth-context"
import { apiCall } from "@/utils/api"
import { Button } from "@/components/ui/custom-button"
import { Loader } from "lucide-react"

export const LoginView = () => {
  const { login } = useAuth()
  const [loading, setLoading] = useState(false)
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    try {
      const data = await apiCall({
        endpoint: "/auth/login",
        method: "POST",
        body: { email, password },
      })
      login(data)
    } catch (e) {
      alert("Error al iniciar sesión")
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="min-h-screen flex items-center justify-center bg-slate-950 p-4">
      <div className="w-full max-w-sm bg-white/10 backdrop-blur-xl border border-white/10 p-8 rounded-3xl shadow-2xl">
        <div className="text-center mb-8">
          <h1 className="text-2xl font-bold text-white">ProxHogar</h1>
          <p className="text-slate-400 text-sm">Bienvenido de nuevo</p>
        </div>

        <form onSubmit={handleLogin} className="space-y-4">
          <input
            className="w-full p-3 rounded-xl bg-slate-900/50 border border-white/10 text-white placeholder:text-slate-500"
            placeholder="cliente@demo.com"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
          />
          <input
            type="password"
            placeholder="Contraseña"
            className="w-full p-3 rounded-xl bg-slate-900/50 border border-white/10 text-white placeholder:text-slate-500"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
          <Button type="submit" className="w-full py-3" disabled={loading}>
            {loading ? <Loader className="animate-spin w-5 h-5" /> : "Iniciar Sesión"}
          </Button>
        </form>

        <div className="mt-6 text-center text-xs text-slate-500">
          <p>Credenciales demo:</p>
          <p
            className="cursor-pointer hover:text-indigo-400"
            onClick={() => {
              setEmail("cliente@test.com")
              setPassword("123456")
            }}
          >
            cliente@test.com / 123456
          </p>
        </div>
      </div>
    </div>
  )
}
